body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
    'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.inline {
  display: inline-block;
}

#logo {
  width: 300px;
  margin-bottom: 90px;
  margin-top: 80px;
}

button.ui.facebookLogin {
  color: white;
  background-image: -webkit-linear-gradient(left, #b91954, #f400f3);
  background-image: linear-gradient(to right, #b91954, #f400f3);
}

.swipeCon{
  margin-top: -16px;
margin-left: -14px;
margin-right: -14px;
  width: auto;
  height: 420px;
  background-image: -webkit-linear-gradient(left, #b91954, #f400f3);
  background-image: linear-gradient(to right, #b91954, #f400f3);
}

div.ui-page[data-url='/'] {
  background-image: url(/images/footer.png);
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: contain;
}

.clickable {
  cursor: pointer;
}

.no-fill {
  fill: none;
}

.profilePic {
  width: 150px;
  height: 150px;
  border-radius: 50%;
}

#welcomeMessage {
  padding-bottom: 20px;
  border-bottom: 1px solid #c3c3c3;
}

#xLogo, #vLogo {
    width: 101px;
    height: 101px;
}

button.signUpButton {
    background-image: -webkit-linear-gradient(left, #b91954, #f400f3) !important;
    background-image: linear-gradient(to right, #b91954, #f400f3) !important;
}

label.signUpMessage {
    font-size: 27px !important;
    margin: 25px !important;
    line-height: 1;
    color: #7d7d7d !important;
}

.question-image {
  margin-left: 20px;
  }

#container {
    width: 500px;
    margin: auto !important;
    display: block;
    height: 500px;
    position: relative;
    list-style-type: none;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.buddy {
    padding: 20px;
    width: 350px;
    height: 360px;
    top: 75px;
    left: 7%;
    position: absolute;
    cursor: hand;
}

.score {
    left: 37px;
    top: 320px;
    width: 55px;
    height: 55px;    border-radius: 39px;
    background-image: -webkit-linear-gradient(left, #b91954, #f400f3);
    background-image: linear-gradient(to right, #b91954, #f400f3);
    position: absolute;
}
.inScore{
    position: absolute;
    margin-top: 18px;
    margin-left: 13px;
    font-size: 18px;
    color: white;
}
.rotate-left {
    -webkit-transform: rotate(30deg) scale(0.8);
            transform: rotate(30deg) scale(0.8);
    -webkit-transition: 1s;
    transition: 1s;
    margin-left: 400px;
    cursor: e-resize;
    opacity: 0;
    z-index: 10;
}

.rotate-right {
    -webkit-transform: rotate(-30deg) scale(0.8);
            transform: rotate(-30deg) scale(0.8);
    -webkit-transition: 1s;
    transition: 1s;
    opacity: 0;
    margin-left: -400px;
    cursor: w-resize;
    z-index: 10;
}

.avatar {
    background: #222;
    width: 310px;
    height: 400px;
    display: block;
    background-size: auto 160% !important;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 10px;
}

.like {
    border-radius: 5px;
    padding: 5px 10px;
    border: 2px solid green;
    color: green;
    text-transform: uppercase;
    font-size: 15px;
    position: absolute;
    top: 50px;
    right: 40px;
    text-shadow: none;
}

.dislike {
    border-radius: 5px;
    padding: 5px 10px;
    border: 2px solid red;
    color: red;
    text-transform: uppercase;
    font-size: 15px;
    position: absolute;
    top: 50px;
    left: 40px;
    text-shadow: none;
}
