body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
    'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.inline {
  display: inline-block;
}

#logo {
  width: 300px;
  margin-bottom: 90px;
  margin-top: 80px;
}

button.ui.facebookLogin {
  color: white;
  background-image: linear-gradient(to right, #b91954, #f400f3);
}

.swipeCon{
  margin-top: -16px;
margin-left: -14px;
margin-right: -14px;
  width: auto;
  height: 420px;
  background-image: linear-gradient(to right, #b91954, #f400f3);
}

div.ui-page[data-url='/'] {
  background-image: url(/images/footer.png);
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: contain;
}

.clickable {
  cursor: pointer;
}

.no-fill {
  fill: none;
}

.profilePic {
  width: 150px;
  height: 150px;
  border-radius: 50%;
}

#welcomeMessage {
  padding-bottom: 20px;
  border-bottom: 1px solid #c3c3c3;
}

#xLogo, #vLogo {
    width: 101px;
    height: 101px;
}

button.signUpButton {
    background-image: linear-gradient(to right, #b91954, #f400f3) !important;
}

label.signUpMessage {
    font-size: 27px !important;
    margin: 25px !important;
    line-height: 1;
    color: #7d7d7d !important;
}

.question-image {
  margin-left: 20px;
  }